import Vue from "vue";
import Router from "vue-router";
import Home from "./components/Home.vue";
import Blogs from "./statics/blogs.json"
import blogEntries from "./statics/data/blogs.json"
Vue.use(Router);

const blogRoutes = Object.keys(blogEntries).map(section => {
 let arr= blogEntries[section].map(post=>{
    // console.log(post)
    return {
      path:`/${section}/${post.id}`,
      name:post.id,
      component:()=>import(`./posts/${post.id}.md`)
    }
  }).flat()
  return arr
});
console.log(blogRoutes.flat())
export default new Router({
  mode: "history",
  base: "/",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path:"/about/InstantPost",
      name:"InstantPost",
      component:()=>import("./posts/Instantpost.md")
    },
    {
      path: "/about",
      name: "About",
      component: () => import("./components/About.vue")
    },
    {
      path: "/projects",
      name: "projects",
      component: () => import("./components/Projects.vue")
    },...blogRoutes.flat()
  ]
});
